import './App.css';
import Home from './components/Home';
import Fouter from './components/layouts/Fouter';
import Header from './components/layouts/Header';
import {BrowserRouter as Router ,Route} from 'react-router-dom';
import ProduitDetaille from './components/produit/ProduitDetaille';
import Login from './components/user/Login';
import Register from './components/user/Register';
import Profil from './components/user/Profil';
import {  useEffect,useState } from 'react'
import { loadUser } from './actions/userAction';
import UpdateProfile from './components/user/updateProfile';
import UpdatePassword  from './components/user/UpdatePassword';
import ForgotPassword from './components/user/ForgotPassword';
import NewPassword from './components/user/NewPassword';
import Cart from './components/cart/Cart';
import Livraison from './components/cart/Livraison';
import ConfirmCommande from './components/cart/ConfirmCommande';
import OrderSuccess from './components/cart/OrderSucces';
import AttentionCommande from './components/cart/AttentionCommande';
import ListOrders from './components/orders/ListOrders';
import OrderDetails from './components/orders/OrderDetails';
import Dashboard from './components/admin/Dashboard';
import ListProduits from './components/admin/ListProducts';
import NewProduct from './components/admin/NewProduct';
import UpdateProduct from './components/admin/UpdateProduct';
import ListMarks from './components/admin/ListeMarks';
import OrdersList from './components/admin/OrdersList';
import ProcessOrder from './components/admin/OrderProcess';
import UsersList from './components/admin/ListUsers';
import UpdateUser from './components/admin/UpdateUser';
import ProductReviews from './components/admin/ProductsReviews';



function App() {
  const [connected, setConnected] = useState(false)
  const [user, setUser] = useState(null)
  const [res, setRes] = useState(null);
  const [erreur, setErreur] = useState(null);
  const [loading, setLoading] = useState(false);
  const [cartLen, setCartLen] = useState(localStorage.getItem('cartItems')?JSON.parse(localStorage.getItem('cartItems')).length:0);
  
   

    useEffect(() => {
      if (erreur) {
        //alert.error(erreur.errMessage?erreur.errMessage:'Erreur!!')
        setErreur(null)
    }
        res&&res.success&&setConnected(true)
        res&&res.success&&setUser(res.user)
    }, [res,erreur])

    useEffect(() => {
      loadUser(setRes,setErreur,setLoading)
    }, [])

  return (
    <Router >
      <div  >
      <Header user={user} loading={loading} cartLen={cartLen} setUser={setUser} fn={setConnected}/>
      
      <div className=" container-fuild" style={{marginLeft:'4%',marginRight:'4%'}}>
          <Route path='/' component={Home}  exact/>
          <Route path="/montres"  ><Home type='Montres'/> </Route>
          <Route path="/lunettes"  ><Home type='Lunettes'/></Route>
          <Route path="/produit/:id"   ><ProduitDetaille  loading={!loading} setCartLen={setCartLen} connected={connected} /></Route>
          <Route path='/panier'  ><Cart   setCartLen={setCartLen} /></Route> 
          <Route path='/succes'  ><OrderSuccess connected={connected} loading={loading}/> </Route>
          <Route path='/attCommande'><AttentionCommande user={user}  setCartLen={setCartLen} loading={loading}/> </Route> 
          <Route path='/confimerCommande'   ><ConfirmCommande setCartLen={setCartLen} user={user}  loading={loading}/> </Route>
          <Route path='/livraison'   ><Livraison user={user}  loading={loading}/> </Route>
          <Route path="/login"   ><Login fn={setConnected} setUser={setUser} connected={connected} loading={loading}/></Route>
          <Route path='/sinscrire'   ><Register fn={setConnected}  setUser={setUser} connected={connected} loading={loading}/></Route>
          <Route path='/motDePasse/oublier'   ><ForgotPassword    connected={connected} loading={loading}/></Route> 
          <Route path='/reset/motDePasse/:token'   ><NewPassword    connected={connected} loading={loading}/></Route> 
          <Route path="/modifier/motPasse/monProfil"   ><UpdatePassword  loading={!loading} connected={connected}   /></Route>
          <Route path="/modifier/monProfil"   ><UpdateProfile user={user} loading={!loading} connected={connected}  setUser={setUser} /></Route>
          <Route path="/monProfil"   ><Profil user={user} loading={!loading} connected={connected} /></Route>
          <Route path="/mesCommandes"   ><ListOrders  loading={!loading} connected={connected} /></Route>
          <Route path="/commande/:id"   ><OrderDetails  loading={!loading} connected={connected} /></Route>
      </div>
      {loading&&user&&user.role==="admin"&&
      <>
        <Route path="/TableauDebord"   ><Dashboard   /></Route>
        <Route path='/admin/produits' component={ListProduits}  exact/>
        <Route path='/admin/marques' component={ListMarks}  />
        <Route path='/admin/commandes' component={OrdersList}  />
        <Route path='/admin/utilisateurs' component={UsersList}  />
        <Route path='/admin/avis' component={ProductReviews}  />
        <Route path='/admin/produit' exact component={NewProduct}  />
        <Route path='/admin/produit/:id' component={UpdateProduct}  />
        <Route path='/admin/commande/:id' component={ProcessOrder}  />
        <Route path='/admin/utilisateur/:id' component={UpdateUser}  />

      </>  
      }

      {loading&&((user&&user.role!=="admin")||!connected)&&<Fouter/> }
      </div>
      

    </Router>  
  );
}

export default App;
