import { useState, useEffect } from 'react'
import MetaData from '../layouts/MetaData'
import Loader from '../layouts/Loader'
import { useAlert } from 'react-alert'
import { useHistory } from "react-router-dom";
import { forgotPasswrod } from '../../actions/userAction'


const ForgotPassword = ({connected,loading }) => {

    const [email, setEmail] = useState('')
    const [res, setRes] = useState(null);
    const [erreur, setErreur] = useState(null);
    const [loading2, setLoding2] = useState(false);
    const alert = useAlert();
    let history = useHistory();
    
    useEffect(() => {
        setLoding2(!loading)
        connected&&history.push('/') 
        res&&res.success&&alert.success(<div style={{fontSize:'10px'}}>{res.message}</div>)
    }, [res,connected,loading])

    useEffect(() => {
      
        if (erreur) {
            alert.error(<div style={{fontSize:'10px'}}>{erreur.errMessage?erreur.errMessage:'Erreur!!'}</div>)
            setErreur(null)
        }
    }, [erreur])

    const submitHandler = (e) => {
        e.preventDefault();
        setLoding2(true)
        forgotPasswrod({email},setRes,setErreur,setLoding2)
    }

    return (
        <>
        {loading2 ? <Loader /> : 
            <>
                <MetaData title={'Mot de passe oubli'} />

                <div className="row wrapper">
                    <div className="col-10 col-lg-5">
                        <form className="shadow-lg" onSubmit={submitHandler}>
                            <h1 className="mb-3">Mot de passe oubli</h1>
                            <div className="form-group">
                                <label htmlFor="email_field">entrer l'adresse e-mail</label>
                                <input
                                    type="email"
                                    id="email_field"
                                    className="form-control"
                                    value={email}
                                    onChange={(e) => setEmail(e.target.value)}
                                    required
                                />
                            </div>

                            <button
                                id="forgot_password_button"
                                type="submit"
                                className="btn btn-block py-3"
                                >
                                Envoyer e-mail
                        </button>

                        </form>
                    </div>
                </div>

            </>}
        </>    
    )
}

export default ForgotPassword