import  {  useEffect,useState } from 'react'
import { Link } from 'react-router-dom'
import { MDBDataTable } from 'mdbreact'

import MetaData from '../layouts/MetaData'
import Loader from '../layouts/Loader'
import Sidebar from './Sidebar'

import { useAlert } from 'react-alert'
import { allUsers,deleteUser } from '../../actions/userAction'


const UsersList = ({ history }) => {

    const alert = useAlert();

    const [res, setRes] = useState(null);
    const [res2, setRes2] = useState(null);
    const [erreur, setErreur] = useState(null);
    const [loading, setLoading] = useState(false)
    useEffect(() => { 
        allUsers(setRes,setErreur) 
        
    }, []) 
    useEffect(() => {
        if (res2&&res2.success) {
            alert.success('Utilisateur supprim avec succs');
            allUsers(setRes,setErreur) 
        }

        if (erreur) {
            alert.error(<div style={{fontSize:'10px'}}>{erreur.errMessage?erreur.errMessage:'Erreur!!'}</div>)
            setErreur(null)
        }
    }, [erreur,res2])
 

    const deleteUserHandler = (id) => {
        setLoading(true)
        deleteUser(id,setRes2,setErreur,setLoading)
    }

    const setUsers = () => {
        const data = {
            columns: [
                {
                    label: 'Email',
                    field: 'email',
                    sort: 'asc'
                },
                {
                    label: 'Nom complet',
                    field: 'name',
                    sort: 'asc'
                },
               
                {
                    label: 'Role',
                    field: 'role',
                    sort: 'asc'
                },
                {
                    label: 'Actions',
                    field: 'actions',
                },
            ],
            rows: []
        }

        res&&res.users.forEach(user => {
            data.rows.push({
                email: user.email,
                name: user.name,
                role: user.role,
                actions: <>
                    <Link to={`/admin/utilisateur/${user._id}`} className="btn btn-primary py-1 px-2">
                        <i className="fa fa-pencil"></i>
                    </Link>
                    <button className="btn btn-danger py-1 px-2 ml-2" disabled={loading} onClick={() => deleteUserHandler(user._id)}>
                        <i className="fa fa-trash"></i>
                    </button>
                </>
            })
        })

        return data;
    }


    return (
        <>
            <MetaData title={'Tous les utilisateurs'} />
            <div className="row">
                <div className="col-12 col-md-2">
                    <Sidebar />
                </div>

                <div className="col-12 col-md-10">
                    <>
                        <h1 className="my-5">Tous les utilisateurs</h1>

                        {!res ? <Loader /> : (
                            <MDBDataTable
                                data={setUsers()}
                                className="px-3"
                                bordered
                                striped
                                hover
                                entriesLabel='Afficher les entres'
                                infoLabel={['Affichage de ', '  ', ' sur ', ' lignes']}
                                paginationLabel= {['Prcdent', 'Suivant']}
                                searchLabel= 'Chercher'
                                noRecordsFoundLabel='Aucun utilisateur correspondante trouv'
                                responsive
                            />
                        )}

                    </>
                </div>
            </div>

        </>
    )
}

export default UsersList