import React, { Fragment, useState, useEffect } from 'react'
import { MDBDataTable } from 'mdbreact'

import MetaData from '../layouts/MetaData'
import Sidebar from './Sidebar'

import { useAlert } from 'react-alert'
import { deleteReview,getReviews } from '../../actions/produitsActions'


const ProductReviews = () => {

    const [productId, setProductId] = useState('')
    const [res, setRes] = useState(null);
    const [res2, setRes2] = useState(null);
    const [erreur, setErreur] = useState(null);
    const [loading, setLoading] = useState(false)
    const alert = useAlert();

    // useEffect(() => { 
    //     if (productId !== '') {
    //         getReviews(productId,setRes,setErreur)
    //     }
        
    // }, []) 
    useEffect(() => {

        if (res2&&res2.success) {
            alert.success('Avis supprim avec succs');
            getReviews(productId,setRes,setErreur)
            setRes2(null)
        }

        if (erreur) {
            alert.error(<div style={{fontSize:'10px'}}>{erreur.errMessage?erreur.errMessage:'Erreur!!'}</div>)
            setErreur(null)
        }
        

    }, [res2, erreur])

    const deleteReviewHandler = (id) => {
        setLoading(true)
        deleteReview(productId, id,setRes2,setErreur,setLoading)
    }

    const submitHandler = (e) => {
        e.preventDefault();
        getReviews(productId,setRes,setErreur)
    }

    const setReviews = () => {
        const data = {
            columns: [
                {
                    label: 'Avis ID',
                    field: 'id',
                    sort: 'asc'
                },
                {
                    label: 'Note',
                    field: 'rating',
                    sort: 'asc'
                },
                {
                    label: 'Commentaire',
                    field: 'comment',
                    sort: 'asc'
                },
                {
                    label: 'Utilisateur',
                    field: 'user',
                    sort: 'asc'
                },
                {
                    label: 'Actions',
                    field: 'actions',
                },
            ],
            rows: []
        }

        res&&res.reviews.forEach(review => {
            data.rows.push({
                id: review._id,
                rating: review.ratting,
                comment: review.comment,
                user: review.name,

                actions:
                    <button className="btn btn-danger py-1 px-2 ml-2"  disabled={loading} onClick={() => deleteReviewHandler(review._id)}>
                        <i className="fa fa-trash"></i>
                    </button>
            })
        })

        return data;
    }

    return (
        <>
            <MetaData title={'Avis sur les produits'} />
            <div className="row">
                <div className="col-12 col-md-2">
                    <Sidebar />
                </div>

                <div className="col-12 col-md-10">
                    <>
                        <div className="row justify-content-center mt-5">
                            <div className="col-5">
                                <form onSubmit={submitHandler}>
                                    <div className="form-group">
                                        <label htmlFor="productId_field">Entrez l'ID du produit</label>
                                        <input
                                            type="text"
                                            id="productId_field"
                                            className="form-control"
                                            value={productId}
                                            onChange={(e) => setProductId(e.target.value)}
                                        />
                                    </div>

                                    <button
                                        id="search_button"
                                        type="submit"
                                        className="btn btn-primary btn-block py-2"
                                    >
                                       OK
								    </button>
                                </ form>
                            </div>

                        </div>

                        {res&&res.reviews.length > 0 ? (
                            <MDBDataTable
                                data={setReviews()}
                                className="px-3"
                                bordered
                                striped
                                hover
                                entriesLabel='Afficher les entres'
                                infoLabel={['', '', '', '']}
                                paginationLabel= {['Prcdent', 'Suivant']}
                                searchLabel= 'Chercher'
                                noRecordsFoundLabel='Aucun Avis correspondante trouv'
                                responsive
                            />
                        ) : (
                                <p className="mt-5 text-center">Aucun Avis.</p>
                            )}


                    </>
                </div>
            </div>

        </>
    )
}

export default ProductReviews