import  {useState,  useEffect } from 'react'
import { Link } from 'react-router-dom';

import MetaData from '../layouts/MetaData'
import Loader from '../layouts/Loader'
import Sidebar from './Sidebar'
import { getAllProducts } from '../../actions/produitsActions';
import { getMarks } from '../../actions/markAction';
import { allUsers } from '../../actions/userAction';

import { useAlert } from 'react-alert';
import { getOrders } from '../../actions/cartAction';




const Dashboard = () => {

    const alert = useAlert();
    const [erreur, setErreur] = useState(null)
    const [produit, setProduit] = useState(null)
    const [mark, setMark] = useState(null)
    const [users, setUsers] = useState(null)
    const [commandes, setCommandes] = useState(null)



    let outOfStock = 0;
    produit && produit.produits.forEach(product => {
        if (product.quantite === 0) {
            outOfStock += 1;
        }
    })

    useEffect(() => {
        getAllProducts(setProduit,setErreur)
        getMarks(setMark,setErreur)
        allUsers(setUsers,setErreur)
        getOrders(setCommandes,setErreur)
    }, [])
  

    useEffect(() => {
        if (erreur) {
            alert.error(erreur.errMessage?erreur.errMessage:'Erreur!!')
            setErreur(null)
        }
    }, [erreur])

    return (
        <>
            <div className="row">
                <div className="col-12 col-md-2">
                    <Sidebar />
                </div>

                <div className="col-12 col-md-10">
                    <h1 className="my-4">Tableau de bord</h1>

                    {!(produit&&mark&&users&&commandes) ? <Loader /> : (
                        <>
                            <MetaData title={'Tableau de bord administrateur'} />

                            <div className="row pr-4">
                                <div className="col-xl-8 col-sm-8 mb-3">
                                    <div className="card text-white bg-primary o-hidden h-100">
                                        <div className="card-body">
                                            <div className="text-center card-font-size">Montant total<br /> <b>{commandes && commandes.montantTotale.toFixed(2)} DTN</b>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-xl-4 col-sm-4 mb-3">
                                    <div className="card text-white bg-danger o-hidden h-100">
                                        <div className="card-body">
                                            <div className="text-center card-font-size">En rupture de stock<br /> <b>{outOfStock}</b></div>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div className="row pr-4">
                                <div className="col-xl-3 col-sm-6 mb-3">
                                    <div className="card text-white bg-success o-hidden h-100">
                                        <div className="card-body">
                                            <div className="text-center card-font-size">Produits<br /> <b>{produit && produit.produitCount}</b></div>
                                        </div>
                                        <Link className="card-footer text-white clearfix small z-1" to="/admin/produits">
                                            <span className="float-left">Voir les dtails</span>
                                            <span className="float-right">
                                                <i className="fa fa-angle-right"></i>
                                            </span>
                                        </Link>
                                    </div>
                                </div>
                                <div className="col-xl-3 col-sm-6 mb-3">
                                    <div className="card text-white bg-warning o-hidden h-100">
                                        <div className="card-body">
                                            <div className="text-center card-font-size">Marques<br /> <b>{mark && mark.marks.length}</b></div>
                                        </div>
                                        <Link className="card-footer text-white clearfix small z-1" to="/admin/marques">
                                            <span className="float-left">Voir les dtails</span>
                                            <span className="float-right">
                                                <i className="fa fa-angle-right"></i>
                                            </span>
                                        </Link>
                                    </div>
                                </div>
                                <div className="col-xl-3 col-sm-6 mb-3">
                                    <div className="card text-white bg-info o-hidden h-100">
                                        <div className="card-body">
                                            <div className="text-center card-font-size">Commandes<br /> <b>{commandes && commandes.commandes.length}</b></div>
                                        </div>
                                        <Link className="card-footer text-white clearfix small z-1" to="/admin/commandes">
                                            <span className="float-left">Voir les dtails</span>
                                            <span className="float-right">
                                                <i className="fa fa-angle-right"></i>
                                            </span>
                                        </Link>
                                    </div>
                                </div>


                                <div className="col-xl-3 col-sm-6 mb-3">
                                    <div className="card text-white bg-secondary o-hidden h-100">
                                        <div className="card-body">
                                            <div className="text-center card-font-size">Utilisateurs<br /> <b>{users && users.users.length}</b></div>
                                        </div>
                                        <Link className="card-footer text-white clearfix small z-1" to="/admin/utilisateurs">
                                            <span className="float-left">Voir les dtails</span>
                                            <span className="float-right">
                                                <i className="fa fa-angle-right"></i>
                                            </span>
                                        </Link>
                                    </div>
                                </div>


                                
                            </div>
                        </>
                    )}

                </div>
            </div>

        </ >
    )
}

export default Dashboard