import  { useState,useEffect } from 'react'
import { MDBDataTable } from 'mdbreact'

import MetaData from '../layouts/MetaData'
import Loader from '../layouts/Loader'
import Sidebar from './Sidebar'
import { getMarks ,deleteMark, newMark,updateMark} from '../../actions/markAction';
import { useAlert } from 'react-alert'


const ListMarks = ({ history }) => {

    const alert = useAlert();
    const [erreur, setErreur] = useState(null)
    const [marks, setMarks] = useState(null)
    const [res, setRes] = useState(null)
    const [loading, setLoading] = useState(false)
    const [loading2, setLoading2] = useState(false)
    const [name, setName] = useState('');
    const [category, setCategory] = useState('Montres');
    const [res2, setRes2] = useState(null)
    const [id, setId] = useState(null)
   

    const categories = ['Montres','Lunettes']

    
    useEffect(() => {
        getMarks(setMarks,setErreur)
    }, [])
  
    const onloadMarque=(id,name,cat)=>{
        setId(id)
        setName(name)
        setCategory(cat)
    }
    useEffect(() => {
     

        res2&&res2.success&&getMarks(setMarks,setErreur)
        res2&&res2.success&&alert.success(!id?'Marque cre avec succs':'Marque mis  jour avec succs')
        res2&&res2.success&&setRes2(null)

        res&&res.success&&getMarks(setMarks,setErreur)
        res&&res.success&&alert.success('Marque supprim avec succs')
        res&&res.success&&setRes(null)

        if (erreur) {
            alert.error(erreur.errMessage)
            setErreur(null)
        }
    }, [erreur,res,res2])

    const setMark = () => {
        const data = {
            columns: [
                {
                    label: 'ID',
                    field: 'id',
                    sort: 'asc'
                },
                {
                    label: 'Nom',
                    field: 'name',
                    sort: 'asc'
                },
                {
                    label: 'Nombre de produit',
                    field: 'numOfAritcle',
                    sort: 'asc'
                },
                {
                    label: 'Catgorie',
                    field: 'category',
                    sort: 'asc'
                },
               
                {
                    label: 'Actions',
                    field: 'actions',
                },
            ],
            rows: []
        }

        marks.marks.forEach(ma => {
            data.rows.push({
                id:ma._id,
                name: ma.name,
                numOfAritcle: `${ma.numOfArticle} pice(s)`,
                category: ma.category,
                actions: <>
                    <button onClick={()=>{onloadMarque(ma._id,ma.name,ma.category)}} className="btn btn-primary py-1 px-2" data-toggle="modal" data-target="#markModal">
                        <i className="fa fa-pencil"></i>
                    </button>
                    <button disabled={loading} className="btn btn-danger py-1 px-2 ml-2" onClick={() => deleteMarkHandler(ma._id,ma.numOfArticle)}>
                        <i className="fa fa-trash"></i>
                    </button>
                </>
            })
        })

        return data;
    }

    const deleteMarkHandler = (id,num) => {
        if(num===0){
            setLoading(true)
            deleteMark(id,setRes,setErreur,setLoading)
        }else{
            alert.error(<div style={{fontSize:'10px'}}>Imposible ! d'effacer cette maraque <br/> car vous avez {num} Article(s) des cette marque</div>)
        }
    }
    const submitHandler = (e) => {
        e.preventDefault();
        
        setLoading2(true)
        id
        ?updateMark(id,{name,category},setRes2,setErreur,setLoading2)
        :newMark({name,category},setRes2,setErreur,setLoading2)
    }
    return (
        <>
            <MetaData title={'Toutes les marques'} />
            <div className="row">
                <div className="col-12 col-md-2">
                    <Sidebar />
                </div>

                <div className="col-12 col-md-10">
                    <>
                        <div className="row">
                            <h1 className="my-5 col-12 col-md-8">Toutes les marques</h1>
                            <button id="review_btn" type="button" onClick={()=>{setId(null);setName('')}} className="btn btn-primary mt-5" style={{height:'3rem'}} data-toggle="modal" data-target="#markModal">
                                Ajouter une marque
                            </button>
                        </div>
                        
                        {!marks ? <Loader /> : (
                            <MDBDataTable
                                data={setMark()}
                                className="px-3"
                                bordered
                                striped
                                hover
                                entriesLabel='Afficher les entres'
                                infoLabel={['Affichage de ', '  ', ' sur ', ' lignes']}
                                paginationLabel= {['Prcdent', 'Suivant']}
                                searchLabel= 'Chercher'
                                noRecordsFoundLabel='Aucune marque correspondante trouve'
                                responsive
                            />
                        )}

                    </>
                </div>
                
                <div className="row mt-2 mb-5">
                                <div className="rating w-50">

                                    <div className="modal fade" id="markModal" tabIndex="-1" role="dialog" aria-labelledby="ratingModalLabel" aria-hidden="true">
                                        <div className="modal-dialog" role="document">
                                            <div className="modal-content">
                                                <div className="modal-header">
                                                    <h5 className="modal-title" id="ratingModalLabel">{!id?'Nouveau marque' :'Mettre  jour la produit'}</h5>
                                                    <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                                        <span aria-hidden="true">&times;</span>
                                                    </button>
                                                </div>
                                                <div className="wrapper my-5">
                            <form className="shadow-lg"  >
                                <h1 className="mb-4">{!id?'Nouveau marque' :'Mettre  jour '}</h1>

                                <div className="form-group">
                                    <label htmlFor="name_field">Nom</label>
                                    <input
                                        type="text"
                                        id="name_field"
                                        className="form-control"
                                        value={name}
                                        onChange={(e) => setName((e.target.value).charAt(0).toUpperCase() + (e.target.value).slice(1))}
                                        required
                                    />
                                </div>
                                

                                <div className="form-group">
                                    <label htmlFor="category_field">Catgorie</label>
                                    <select className="form-control" id="category_field" value={category} onChange={(e) => {setCategory(e.target.value)}}>
                                        {categories.map(category => (
                                            <option key={category} value={category} >{category}</option>
                                        ))}

                                    </select>
                                </div>
                                
                                <button
                                    id="login_button"
                                    type="submit"
                                    className="btn btn-block py-3"
                                    disabled={loading2}
                                    onClick={submitHandler}
                                    data-dismiss="modal" aria-label="Close"
                                >
                                    {!id?'Ajouter' :'Mettre  jour '}
                                </button>

                            </form>
                        </div>
                                            </div>
                                        </div>
                                    </div>

                                </div>
                            </div>
            </div>

        </>
    )
}

export default ListMarks