import '../../App.css'
import {  Link } from 'react-router-dom'
import { useState, useEffect } from 'react';
import {  logout } from '../../actions/userAction';
import { useAlert } from 'react-alert';

function Header({user,loading,setUser,fn,cartLen}) {
    const [click, setClick] = useState(false);
    const [button, setButton] = useState(true);
    const alert = useAlert();
    const handleClick = () => setClick(!click);
    const closeMobileMenu = () => setClick(false);
  
    const showButton = () => {
      if (window.innerWidth <= 960) {
        setButton(false);
      } else {
        setButton(true);
      }
    };
    
    useEffect(() => {
      showButton();
    }, [user]);
    const logoutHandler = () => {
        logout()
        setUser(null)
        fn(false)
        alert.success('Dconnect avec succs.')
    }

    const elementC=user ? 
        <div className="ml-4 dropdown d-inline">
            <Link to="#!" className="btn dropdown-toggle text-white mr-4" type="button" id="dropDownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">

                <figure className="avatar avatar-nav">
                    <img
                        src={user &&`/${user.avatar?user.avatar:'images/default_avatar.jpg'}`}
                        className="rounded-circle"
                        alt='image_profile'
                    />
                </figure>
                <span>{user && (button?user.name:user.name.substring(0,9))}</span>
            </Link>

             <div className="dropdown-menu" aria-labelledby="dropDownMenuButton">

                {user && user.role === 'admin' && (
                    <Link className="dropdown-item" to="/TableauDebord">Tableau de bord</Link>
                )}
                <Link className="dropdown-item" to="/mesCommandes">Commandes</Link>
                <Link className="dropdown-item" to="/monProfil">Profil</Link>
                <Link className="dropdown-item text-danger" to="/"  onClick={logoutHandler}>
                    Dconnexion
                </Link>

            </div> 


        </div> :loading&&<Link to="/login" className="btn ml-4" id="login_btn"><i className='	glyphicon glyphicon-log-in'  aria-hidden="true"></i> CONNEXION</Link>    

    window.addEventListener('resize', showButton);
    return (
        <>
            
        <nav className='navbar'>
        <div className='navbar-container'>
          <Link to="/" className='navbar-logo' onClick={closeMobileMenu}>
              <img src="/images/logoL.png" alt='image_logo' width={button?'25%':'50'} />
              <span className='navbar-logo-name' >Luxe Store</span>
          </Link>
          <div className='menu-icon2' hidden={button} >
              {elementC}
                <Link to="/panier" style={{ textDecoration: 'none',marginleft:'2px',color:'white' }} >
                    <i className='fa fa-shopping-cart' />
                    <span className="ml-1" id="cart_count">{cartLen}</span>
                </Link> 
          </div>
          <div className='menu-icon' onClick={handleClick}>         
            <i className={click ? 'fa fa-times' : 'fa fa-bars'} />
          </div>
         
          <ul className={click ? 'nav-menu active' : 'nav-menu'}>
              
            <li className='nav-item'>
              <Link to='/' className='nav-links' onClick={closeMobileMenu}>
                Accueil
              </Link>
            </li>
            <li className='nav-item'>
              <Link
                to='/montres'
                className='nav-links'
                onClick={closeMobileMenu}
              >
                Montres
              </Link>
            </li>
            <li className='nav-item'>
              <Link
                to='/lunettes'
                className='nav-links'
                onClick={closeMobileMenu}
              >
                Lunettes
              </Link>
            </li>
            <span hidden={button}>
            <li className='nav-item' ><Link className='nav-links'  to='#'></Link></li>
            <li className='nav-item' ><Link className='nav-links' to='#'></Link></li>
            <li className='nav-item' ><Link className='nav-links' to='#'></Link></li>
            <li className='nav-item' ><Link className='nav-links' to='#'></Link></li>
            <li className='nav-item' ><Link className='nav-links' to='#'></Link></li>
            <li className='nav-item' ><Link className='nav-links' to='#'></Link></li>
            </span>

            <li className='nav-item'  hidden={!button}> 
                <span className='btnLogout'>
                <Link to="/panier" style={{ textDecoration: 'none',color:'white' }} >
                        <i className='fa fa-shopping-cart' />
                        <span className="ml-1" id="cart_count">{cartLen}</span>
                 </Link>
                </span> 
            </li>

            <li className='nav-item' hidden={!button}>
                
                <span className='btnLogout'>
                {elementC}   
                </span> 
            </li>
            
          </ul>
        </div>
      </nav>
        </>
    )
}

export default Header
