import axios from 'axios';



export const getMarks = async(fnp,fne,type) => {
     let link=type?`/mark?category=${type}`:'/mark'
     await axios.get(link)
     .then(({data})=>{
        fnp(data)
     }).catch((e)=>{
        fne(e.response.data)
        
     })
}

export const getMark = async(id,fnp,fne) => {
   
   await axios.get(`/mark/${id}`)
   .then(({data})=>{
      fnp(data)
   }).catch((e)=>{
      fne(e.response.data)
      
   })
}

export const newMark = async(markData,fnp,fne,fnl) => {
   const config={headres:{"content-Type":'application/json'}}
   await axios.post('/mark',markData,config).then(({data})=>{
      fnp(data)
   }).catch((e)=>{
     if(e.response)
      fne(e.response.data)   
   })
   fnl(false)
}


export const updateMark = async(id,markData,fnp,fne,fnl) => {
   const config={headres:{"content-Type":'application/json'}}
   await axios.put('/mark/'+id,markData,config).then(({data})=>{
      fnp(data)
   }).catch((e)=>{
     if(e.response)
      fne(e.response.data)   
   })
   fnl(false)
}
export const deleteMark = async(id,fnp,fne,fnL) => {
   await axios.delete(`/mark/${id}`).then(({data})=>{
      fnp(data)
   }).catch((e)=>{
     if(e.response)
      fne(e.response.data)
   })
   fnL(false)
}
