import  { useState,useEffect } from 'react'
import { Link } from 'react-router-dom'
import { MDBDataTable } from 'mdbreact'

import MetaData from '../layouts/MetaData'
import Loader from '../layouts/Loader'
import Sidebar from './Sidebar'
import { getAllProducts,deleteProduct } from '../../actions/produitsActions';
import { useAlert } from 'react-alert'


const ListProduits = ({ history }) => {

    const alert = useAlert();
    const [erreur, setErreur] = useState(null)
    const [produit, setProduit] = useState(null)
    const [res, setRes] = useState(null)
    const [loading, setLoading] = useState(false)


    
    useEffect(() => {
        getAllProducts(setProduit,setErreur)
    }, [])
  

    useEffect(() => {
        res&&res.success&&getAllProducts(setProduit,setErreur)
        res&&res.success&&alert.success('Produit supprim avec succs')
        if (erreur) {
            alert.error(erreur.errMessage?erreur.errMessage:'Erreur!!')
            setErreur(null)
        }
    }, [erreur,res])

    const setProducts = () => {
        const data = {
            columns: [
                {
                    label: 'Refrence',
                    field: 'ref',
                    sort: 'asc'
                },
                {
                    label: 'Nom',
                    field: 'name',
                    sort: 'asc'
                },
                {
                    label: 'Prix DTN',
                    field: 'price',
                    sort: 'asc'
                },
                {
                    label: 'Stock',
                    field: 'stock',
                    sort: 'asc'
                },
                {
                    label: 'Actions',
                    field: 'actions',
                },
            ],
            rows: []
        }

        produit.produits.forEach(product => {
            data.rows.push({
                ref: product.ref,
                name: product.name,
                price: product.prix ,
                stock: product.quantite,
                actions: <>
                    <Link to={`/admin/produit/${product._id}`} className="btn btn-primary py-1 px-2">
                        <i className="fa fa-pencil"></i>
                    </Link>
                    <button disabled={loading} className="btn btn-danger py-1 px-2 ml-2" onClick={() => deleteProductHandler(product._id)}>
                        <i className="fa fa-trash"></i>
                    </button>
                </>
            })
        })

        return data;
    }

    const deleteProductHandler = (id) => {
        setLoading(true)
        deleteProduct(id,setRes,setErreur,setLoading)
    }

    return (
        <>
            <MetaData title={'Tous les produits'} />
            <div className="row">
                <div className="col-12 col-md-2">
                    <Sidebar />
                </div>

                <div className="col-12 col-md-10">
                    <>
                        <h1 className="my-5">Tous les produits</h1>

                        {!produit ? <Loader /> : (
                            <MDBDataTable
                                data={setProducts()}
                                className="px-3"
                                bordered
                                striped
                                hover
                                entriesLabel='Afficher les entres'
                                infoLabel={['Affichage de ', '  ', ' sur ', ' lignes']}
                                paginationLabel= {['Prcdent', 'Suivant']}
                                searchLabel= 'Chercher'
                                noRecordsFoundLabel='Aucun Produit correspondant trouv'
                                responsive
                            />
                        )}

                    </>
                </div>
            </div>

        </>
    )
}

export default ListProduits