import axios from 'axios';



export const  addItem= async(id,qte,fnCart,fn,) => {
    const {data} = await axios.get('/produit/'+id)
    const item={
                 product:id,
                 name:data.produit.name,
                 prix:data.produit.prix,
                 image:data.produit.images[0].url,
                 quantite:data.produit.quantite,
                 qte  
                }
    let cartItems =  localStorage.getItem('cartItems')?JSON.parse(localStorage.getItem('cartItems')):[]
    const index =  cartItems.findIndex(i=>i.product===item.product)
    index!==-1? cartItems[index]=item:cartItems.push(item)
    fnCart(cartItems.length)
    localStorage.setItem('cartItems',JSON.stringify(cartItems))
    fn&&fn(cartItems)
    
}
export const  removeItemFromCart= async(id,fnCart,fn) => {
    let cartItems =  localStorage.getItem('cartItems')?JSON.parse(localStorage.getItem('cartItems')):[]
    const index =  cartItems.findIndex(i=>i.product===id)
    cartItems.splice(index,1)
    fnCart(cartItems.length)
    localStorage.setItem('cartItems',JSON.stringify(cartItems))
    fn(cartItems)  
}

export const  saveShippingInfo= async(data) => {
    
    localStorage.setItem('livraisonInfos',JSON.stringify(data))

    
}

export const createOrder = async(data,fnp,fne,fnL) => {
    const config={headres:{"content-Type":'application/json'}}
    await axios.post('/commande',data,config)
     .then(({data})=>{
        fnp(data)
     }).catch((e)=>{
        if(e.response) 
        fne(e.response.data)
     })
     fnL(false)
}

