import MetaData from './layouts/MetaData'
import Pagination from 'react-js-pagination'
import Slider from 'rc-slider'
import 'rc-slider/assets/index.css';
import { useAlert } from 'react-alert';
import { getProducts} from '../actions/produitsActions'
import {  useEffect,useState } from 'react'
import Produit from './produit/Produit';
import Loader from './layouts/Loader';
import { Row,Col } from 'react-bootstrap';
import Search from './layouts/Search';
import FiltreMark from './layouts/FiltreMark';
import { Carousel } from 'react-bootstrap'
import NotFoundProduct from './layouts/NotFoundProduit';

const { createSliderWithTooltip } = Slider;
const Range = createSliderWithTooltip(Slider.Range)

function Home({type}) {
    const alert = useAlert();
    const [produits, setProduits] = useState(null)
    const [erreur, setErreur] = useState(null)
    const [currentPage, setCurrentPage] = useState(1)
    const [cpt, setCpt] = useState(0)
    const [price, setPrice] = useState([1,3000])
    const [category, setCategory] = useState(type?type:'Tous les produits')
    const [tricroi, setTricroi] = useState(false)
    const [tri, setTri] = useState('default')
    const categories = ["Montres","Lunettes",'Tous les produits']
    const [rating, setRating] = useState(0)
    const [divF, setDivF] = useState(false)
    const [keyword, setKeyword] = useState('')
    const [mark, setMark] = useState([])
    
    function topFunction() {
        document.getElementById("myDiv").scrollIntoView();
      }

    useEffect(() => {
       type&&produits&&produits.success&&topFunction()  
     }, [produits])
    useEffect(() => {
       cpt!==0&&produits&&produits.success&&topFunction()
       produits&&produits.success&&setCpt(cpt=>cpt+1)
    }, [produits])

    useEffect(() => {
        setCurrentPage(1)
    }, [keyword,price,rating,tri,tricroi,category,mark])

    useEffect(() => {
        setProduits(null)
    }, [currentPage,keyword,price,rating,tri,tricroi,category,mark])

    useEffect(() => {
        showButton()
        setErreur(null)
        getProducts(setProduits,setErreur,currentPage,keyword,price,rating,tri,tricroi,category,mark)
    }, [currentPage,keyword,price,rating,tri,tricroi,category,mark])
    
    const showButton = () => {
        if (window.innerWidth <= 767) {
            setDivF(true);
        } else {
            setDivF(false);
        }
      };
    window.addEventListener('resize', showButton);
    useEffect(() => {
        if (erreur) {
            alert.error(<div style={{fontSize:'10px'}}>{erreur.errMessage?erreur.errMessage:'Erreur!!'}</div>)
            setErreur(null)
        }
    }, [erreur])

    const setCurrentPageNo=(pageNumber)=> {
        setCurrentPage(pageNumber)
    }
    
    
    return (
        <>
            <MetaData title='acheter les meilleurs produits en ligne'/>
            
            <Carousel  pause={false} hidden={type} > 
                <Carousel.Item >
                    <img className="my-5 img-fluid d-block mx-auto" alt='img_header2' src="/images/header2.png"  width='92%' />
                </Carousel.Item>
                <Carousel.Item >
                    <img className="my-5 img-fluid d-block mx-auto" alt='img_header1' src="/images/header1.png"  width='120%'  />
                </Carousel.Item>
                <Carousel.Item >
                    <img className="my-5 img-fluid d-block mx-auto" alt='img_header3' src="/images/header3.png"  width='120%' />
                </Carousel.Item>
            </Carousel>
            <div  id="myDiv" align='right'> {new Date().toUTCString().substr(0,17) } </div>
            <Row >
                <Col lg='4' sm='12'>
                    <h1 id="products_heading" >{type?type:'Tous les produits'}</h1>
                </Col>
                <Col lg='4' sm='12' >
                    <Search fn={setKeyword} /> 
                </Col>
                <Col lg='4' sm='12' >
                   <div className="input-group">
                                         <select  className="custom-select" onChange={(event)=>{setTri(event.target.value)}}>
                                            <option value='default'>Trier par date </option>
                                            <option value='name' >Trier par Nom</option>
                                            <option value="prix">Trier par Prix</option>
                                            <option value="rate">Trier par les avis </option>  
                                        </select>  
                                        <button id="sort_btn" className="btn"    title={tricroi?'Croissant':'Decroissant'}  onClick={()=>setTricroi(!tricroi)}>
                                                 <i className={tricroi?'fa fa-sort-amount-asc':'fa fa-sort-amount-desc'}   aria-hidden="true"></i>
                                        </button> 
                    </div> 
                    <br hidden={!divF}/>
                    <button id="sort_btn" className="btn"  hidden={!divF} data-toggle="modal" data-target="#filtreModal" >
                                                 <i className='	fa fa-sliders'  aria-hidden="true"></i> FILTRER <i className='	fa fa-sliders'  aria-hidden="true"></i>
                     </button>                                      
                </Col>
            </Row>
            
           
                
            
            <section id="products" className=" mt-5">
                <div className="row">
                                    <div className="col-6 col-md-3 mt-5 mb-5" hidden={divF}>
                                        <div className="px-5">  
                                        <Range
                                                marks={{
                                                    1: `${price[0]} DT`,
                                                    3000: `${price[1]} DT`
                                                }}
                                                min={1}
                                                max={3000}
                                                defaultValue={[1, 3000]}
                                                tipFormatter={value => `${value}`}
                                                tipProps={{
                                                    placement: "top",
                                                    visible: false
                                                }}
                                                value={price}
                                                onChange={price => setPrice(price)}
                                            
                                            />

                                            <hr className="my-5" />

                                             {type?<FiltreMark type={type} handleFilters={setMark}/>
                                             :<div className="mt-5">
                                                <h4 className="mb-3">
                                                    Catgories
                                                </h4>

                                                <ul className="pl-0">
                                                    {categories.map(categor => (
                                                        <li
                                                            style={categor===category
                                                            ?{
                                                                cursor: 'pointer',
                                                                listStyleType: 'none',
                                                                color:'#fa9c23',
                                                                textDecoration:'underline',
                                                                fontWeight:600
                                                            }
                                                            :
                                                            {
                                                            cursor: 'pointer',
                                                            listStyleType: 'none'
                                                            }}
                                                            key={categor}
                                                            onClick={(e) => {setCategory(categor)}}
                                                        >
                                                            {categor}
                                                        </li>
                                                    ))}
                                                </ul>
                                            </div>} 

                                            <hr className="my-3" />

                                            <div className="mt-5">
                                                <h4 className="mb-3">
                                                    Avis
                                                </h4>

                                                <ul className="pl-0">
                                                    {[5, 4, 3, 2, 1].map(star => (
                                                        <li
                                                            style={{
                                                                cursor: 'pointer',
                                                                listStyleType: 'none'
                                                            }}
                                                            key={star}
                                                            onClick={() => setRating(star)}
                                                        >
                                                            <div className="rating-outer">
                                                                <div className="rating-inner"
                                                                    style={{
                                                                        width: `${star * 20}%`
                                                                    }}
                                                                >
                                                                </div>
                                                            </div>
                                                        </li>
                                                    ))}
                                                </ul>
                                            </div>

                                        </div>
                                    </div>

                                    <div className="col-12 col-md-9">
                                        <div className="row">
                                        {  produits? <>
                                        {produits.produitCount>0? produits.produits.map(product => (
                                        <Produit key={product._id} produit={product} col={4} />
                                          )):<NotFoundProduct/>}</>  :<Loader/>              
                                        } 
                                        </div>
                                    </div>
                                
                                   
                </div>
            </section>
            
            <div className="d-flex justify-content-center mt-5">
            {  produits&&produits.produitCount>0&&(<Pagination
                activePage={currentPage}
                itemsCountPerPage={produits.resPerPage}
                totalItemsCount={produits.produitCount}
                onChange={setCurrentPageNo}
                nextPageText={'Suiv'}
                prevPageText={'Prec'}
                firstPageText={'Premier'}
                lastPageText={'Dernier'}
                itemClass="page-item"
                linkClass="page-link"
                responsive
            />)}
            </div>
            <div className="row mt-2 mb-5"  hidden={!divF}>
                                <div className="rating w-50">

                                    <div style={{width:'70%',marginLeft:'20%'}} className="modal fade" id="filtreModal" tabIndex="-1" role="dialog" aria-labelledby="ratingModalLabel" aria-hidden="true">
                                        <div className="modal-dialog" role="document">
                                            <div className="modal-content">
                                                <div className="modal-header">
                                                    <h5 className="modal-title" id="ratingModalLabel">Zone de Filtrage</h5>
                                                    <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                                        <span aria-hidden="true">&times;</span>
                                                    </button>
                                                </div>
                                                <div className="modal-body">

                                                <div  >
                                                        <div className="px-2">  
                                                        <Range
                                                                marks={{
                                                                    1: `${price[0]} DT`,
                                                                    3000: `${price[1]} DT`
                                                                }}
                                                                min={1}
                                                                max={3000}
                                                                defaultValue={[1, 3000]}
                                                                tipFormatter={value => `${value}`}
                                                                tipProps={{
                                                                    placement: "top",
                                                                    visible: false
                                                                }}
                                                                value={price}
                                                                onChange={price => setPrice(price)}
                                                            
                                                            />

                                                            <hr className="my-5" />

                                                            {type?<FiltreMark type={type} handleFilters={setMark}/>
                                                            :<div className="mt-5">
                                                                <h4 className="mb-3">
                                                                    Catgories
                                                                </h4>

                                                                <ul className="pl-0">
                                                                    {categories.map(categor => (
                                                                        <li
                                                                            style={categor===category
                                                                            ?{
                                                                                cursor: 'pointer',
                                                                                listStyleType: 'none',
                                                                                color:'#fa9c23',
                                                                                textDecoration:'underline',
                                                                                fontWeight:600
                                                                            }
                                                                            :
                                                                            {
                                                                            cursor: 'pointer',
                                                                            listStyleType: 'none'
                                                                            }}
                                                                            key={categor}
                                                                            onClick={(e) => {setCategory(categor)}}
                                                                        >
                                                                            {categor}
                                                                        </li>
                                                                    ))}
                                                                </ul>
                                                            </div>} 

                                                            <hr className="my-3" />

                                                            <div className="mt-5">
                                                                <h4 className="mb-3">
                                                                    Avis
                                                                </h4>

                                                                <ul className="pl-0">
                                                                    {[5, 4, 3, 2, 1].map(star => (
                                                                        <li
                                                                            style={{
                                                                                cursor: 'pointer',
                                                                                listStyleType: 'none'
                                                                            }}
                                                                            key={star}
                                                                            onClick={() => setRating(star)}
                                                                        >
                                                                            <div className="rating-outer">
                                                                                <div className="rating-inner"
                                                                                    style={{
                                                                                        width: `${star * 20}%`
                                                                                    }}
                                                                                >
                                                                                </div>
                                                                            </div>
                                                                        </li>
                                                                    ))}
                                                                </ul>
                                                            </div>
                                                            <div align="right"><button id='sort_btn'   className="btn" data-dismiss="modal" aria-label="Close">ok</button></div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>

                                </div>
                            </div>

        </>
    )
}

export default Home
