import { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { MDBDataTable } from 'mdbreact'

import MetaData from '../layouts/MetaData'
import Loader from '../layouts/Loader'
import Sidebar from './Sidebar'

import { useAlert } from 'react-alert'
import { getOrders ,deleteOrder} from '../../actions/cartAction'

const OrdersList = ({ history }) => {


    const [res, setRes] = useState(null);
    const [res2, setRes2] = useState(null);
    const [erreur, setErreur] = useState(null);
    const [loading, setLoading] = useState(false)

    const alert = useAlert()
 
    const deleteOrderHandler = (id) => {
        setLoading(true)
        deleteOrder(id,setRes2,setErreur,setLoading)
    }
    useEffect(() => { 
        getOrders(setRes,setErreur)  
    }, []) 
    useEffect(() => {
        if (res2&&res2.success) {
            getOrders(setRes,setErreur)  
            alert.success('Commande supprime avec succs');
        }

        if (erreur) {
            alert.error(<div style={{fontSize:'10px'}}>{erreur.errMessage?erreur.errMessage:'Erreur!!'}</div>)
            setErreur(null)
        }
    }, [erreur,res2])
    function reverseString(str) {return str.substring(6,10)+'/'+str.substring(3,5)+'/'+str.substring(0,2)   }
    const setOrders = () => {
        const data = {
            columns: [
                {
                    label: 'Commande ID',
                    field: 'id',
                    sort: 'asc'
                },
                {
                    label: 'Date',
                    field: 'date',
                    sort: 'asc'
                },
                {
                    label: "Nombre d'objets",
                    field: 'numOfItems',
                    sort: 'asc'
                },
                {
                    label: 'Montant DTN',
                    field: 'amount',
                    sort: 'asc'
                },
                {
                    label: 'Statut',
                    field: 'status',
                    sort: 'asc'
                },
                {
                    label: 'Actions',
                    field: 'actions',
                    sort: 'asc'
                },
            ],
            rows: []
        }

        res&&res.commandes.forEach(order => {
            data.rows.push({
                id: order._id,
                date:reverseString(new Date(order.dateCreation).toLocaleDateString()),
                numOfItems: order.orderItems.length,
                amount: order.prixTotale,
                status: order.orderStatus,
                actions:
                <>
                    <Link to={`/admin/commande/${order._id}`}  className="btn btn-primary">
                        <i className="fa fa-eye"></i>
                    </Link>
                    <button className="btn btn-danger py-1 px-2 ml-2" disabled={loading} onClick={() => deleteOrderHandler(order._id)}>
                        <i className="fa fa-trash"></i>
                    </button>
              </>

            })
        })

        return data;
    }

    return (
        <>

            <MetaData title={'Mes Commandes'} />
            <div className="row">
                <div className="col-12 col-md-2">
                    <Sidebar />
                </div>

                <div className="col-12 col-md-10">
                    <>
                        <h1 className="my-5">Mes Commandes</h1>

                        {!res ? <Loader /> : (
                            <MDBDataTable
                                data={setOrders()}
                                className="px-3"
                                bordered
                                striped
                                hover
                                entriesLabel='Afficher les entres'
                                infoLabel={['Affichage de ', '  ', ' sur ', ' lignes']}
                                paginationLabel= {['Prcdent', 'Suivant']}
                                searchLabel= 'Chercher'
                                noRecordsFoundLabel='Aucune commande correspondante trouve'
                                responsive
                            />
                        )}

                    </>
                </div>
            </div>


            

        </>
    )
}

export default OrdersList
