import  {  useState, useEffect } from 'react'
import { Link } from 'react-router-dom'

import MetaData from '../layouts/MetaData'

import { useAlert } from 'react-alert'
import { useHistory } from "react-router-dom";
import { login } from '../../actions/userAction'
import Loader from '../layouts/Loader'

const Login = ({connected,fn,setUser,loading }) => {

    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [res, setRes] = useState(null);
    const [erreur, setErreur] = useState(null);
    const [loading2, setLoding2] = useState(false);
    let history = useHistory();
    const alert = useAlert();
    const redirect = history.location.search ? history.location.search.split('=')[1] : '/'

    useEffect(() => {
        setLoding2(!loading)
      
        if(!loading2) document.getElementById("chekOut").scrollIntoView();
        connected&&history.push('/') 
        res&&res.success&&fn(true)
        res&&res.success&&setUser(res.user)
        res&&res.success&&history.push(redirect)
        
    }, [res,connected,loading])
    useEffect(() => {
        if (erreur) {
            alert.error(<div style={{fontSize:'10px'}}>{erreur.errMessage?erreur.errMessage:'Erreur!!'}</div>)
            setErreur(null)
        }
    }, [erreur])

    const submitHandler = (e) => {
        e.preventDefault();
        setLoding2(true)
        login(email, password,setRes,setErreur,setLoding2)
    }

    return (
        <>
            {loading2 ? <Loader /> : (
                <>
                <span id='chekOut' style={{opacity:'0.0',fontSize:'x-small'}}>aaaaaaa</span>

                    <MetaData title={'Connexion'} />

                    <div className="row wrapper" >
                        <div className="col-10 col-lg-5" >
                            <form className="shadow-lg"  onSubmit={submitHandler}>
                                <h1 align='center' style={{fontSize:'2rem'}} >CONNEXION</h1>
                                <div className="form-group">
                                    <label htmlFor="email_field">Email</label>
                                    <input
                                        type="email"
                                        id="email_field"
                                        className="form-control"
                                        value={email}
                                        onChange={(e) => setEmail(e.target.value)}
                                    />
                                </div>

                                <div className="form-group">
                                    <label htmlFor="password_field">Mot de passe</label>
                                    <input
                                        type="password"
                                        id="password_field"
                                        className="form-control"
                                        value={password}
                                        onChange={(e) => setPassword(e.target.value)}
                                    />
                                </div>

                                <Link to="/motDePasse/oublier" className="float-right mb-4">Mot de passe oubli?</Link>

                                <button
                                    id="login_button"
                                    type="submit"
                                    className="btn btn-block py-3"
                                >
                                    CONNEXION
                                </button>

                                <Link to={`${!redirect?'/sinscrire':'/sinscrire?url=/confimerCommande'}`} className="float-right mt-3">nouvel utilisateur?</Link>
                            </form>
                        </div>
                    </div>


                </>
            )}
        </>
    )
}

export default Login