import { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import MetaData from '../layouts/MetaData'
import Loader from '../layouts/Loader'
import Sidebar from './Sidebar'
import { useAlert } from 'react-alert'
import { getOrder, updateOrder } from '../../actions/cartAction';


const ProcessOrder = ({ match }) => {

    const [status, setStatus] = useState('');
    const [res, setRes] = useState(null);
    const [res2, setRes2] = useState(null);
    const [loading, setLoading] = useState(false)
    const [erreur, setErreur] = useState(null);
    const alert = useAlert();
   
    const orderId = match.params.id;
    useEffect(() => { 
        getOrder(orderId,setRes,setErreur) 
        
    }, [orderId]) 

    useEffect(() => {
        if (erreur) {
            alert.error(<div style={{fontSize:'10px'}}>{erreur.errMessage?erreur.errMessage:'Erreur!!'}</div>)
            setErreur(null)
        }
        if (res2&&res2.success) {
            alert.success('Commande mise  jour avec succs');
            getOrder(orderId,setRes,setErreur)      
        }

    }, [erreur,res2])

   

    const updateOrderHandler = (id) => {
        setLoading(true)
        updateOrder(id, {status},setRes2,setErreur,setLoading)
    }

    const shippingDetails = res && `${res.commande.shippingInfo.address}, ${res.commande.shippingInfo.city}, ${res.commande.shippingInfo.postalCode}, ${res.commande.shippingInfo.gouvernorat}`

    return (
        <>
            <MetaData title={`Traiter la commande # ${res && res.commande._id}`} />
            <div className="row">
                <div className="col-12 col-md-2">
                    <Sidebar />
                </div>

                <div className="col-12 col-md-10">
                    <>
                        {!res ? <Loader /> : (
                            <div className="row d-flex justify-content-around">
                                <div className="col-12 col-lg-7 order-details">

                                <h1 className="my-5">Commande # {res.commande._id}</h1>

                                <h4 className="mb-4">Informations sur la livraison</h4>
                                <p><b>Nom complet:</b> {res.commande.shippingInfo.name}</p>
                                <p><b>Num tel :</b> {res.commande.shippingInfo.phoneNo}</p>
                                <p className="mb-4"><b>Adresse:</b> {shippingDetails}</p>
                                <p><b>Montant :</b> {res.commande.prixTotale} DTN</p>

                                <hr />
                                <h4 className="my-4">Date de la commande :</h4>
                                <p >{new Date(res.commande.dateCreation).toLocaleDateString()} | {new Date(res.commande.dateCreation).toLocaleTimeString()} </p>


                                <h4 className="my-4">Statut de la commande :</h4>
                                <p className={res.commande.orderStatus && String(res.commande.orderStatus).includes('Livre') ? "greenColor" : "redColor"} ><b>{res.commande.orderStatus}</b></p>


                                <h4 className="my-4">Items commands:</h4>

                                <hr />
                                <div className="cart-item my-1">
                                    {res.commande.orderItems.map(item => (
                                        <div key={item.product} className="row my-5">
                                            <div className="col-4 col-lg-2">
                                                <img src={item.image} alt={item.name} height="45" width="65" />
                                            </div>

                                            <div className="col-5 col-lg-5">
                                                <Link to={`/produit/${item.product}`}>{item.name}</Link>
                                            </div>


                                            <div className="col-4 col-lg-2 mt-4 mt-lg-0">
                                                <p>{item.prix} DTN</p>
                                            </div>

                                            <div className="col-4 col-lg-3 mt-4 mt-lg-0">
                                                <p>{item.qte} Pice(s)</p>
                                            </div>
                                        </div>
                                    ))}
                                </div>
                                    <hr />
                                </div>

                                <div className="col-12 col-lg-3 mt-5">
                                    <h4 className="my-4">Statut</h4>

                                    <div className="form-group">
                                        <select
                                            className="form-control"
                                            name='status'
                                            value={status}
                                            onChange={(e) => setStatus(e.target.value)}
                                        >
                                            <option value="En traitement">En traitement</option>
                                            <option value="Expdie">Expdie</option>
                                            <option value="livraison">En livraison</option>
                                            <option value="Livre">Livre</option>
                                        </select>
                                    </div>

                                    <button className="btn btn-primary btn-block" disabled={loading} onClick={() => updateOrderHandler(res.commande._id)}>
                                        Valider 
                                    </button>
                                </div>

                            </div>
                        )}
                    </>
                </div>
            </div>

        </>
    )
}

export default ProcessOrder