import axios from 'axios';



export const login = async(email,password,fnp,fne,fnL) => {
    const config={headres:{"content-Type":'application/json'}}
    await axios.post('/user/login',{email,password},config)
     .then(({data})=>{
        fnp(data)
        fnL(false)
     }).catch((e)=>{
        fnL(false)
         if(e.response) 
        fne(e.response.data)
     })
     
}

export const register = async(userData,fnp,fne,fnL) => {
    const config={headres:{"content-Type":'multipart/form-data'}}
    await axios.post('/user',userData,config)
     .then(({data})=>{
        fnp(data)
     }).catch((e)=>{
        if(e.response) 
        fne(e.response.data)
     })
     fnL(false)
}
