export const resertPassword = async(passwords,token,fnp,fne,fnL) => {
   const config={headres:{"content-Type":'application/json'}}
  await axios.put('/user/reset/pswd/'+token,passwords,config)
   .then(({data})=>{
      fnp(data)
   }).catch((e)=>{
      if(e.response) 
      fne(e.response.data)
   })
   fnL(false)
}

