
import { Link } from 'react-router-dom'
import MetaData from '../layouts/MetaData'
import  { useState,useEffect } from 'react'
import { addItem ,removeItemFromCart} from '../../actions/cartAction';
import { useHistory} from "react-router-dom";
import NotFoundProduct from '../layouts/NotFoundProduit';
import { Fragment } from 'react';

const Cart = ({ setCartLen }) => {

    const [cartItems, setCartItems] = useState(localStorage.getItem('cartItems')?JSON.parse(localStorage.getItem('cartItems')):[])

    let history=useHistory()
    const removeCartItemHandler = (id) => {
    removeItemFromCart(id,setCartLen,setCartItems)
    }
    useEffect(() => {
        cartItems.length != 0 && document.getElementById("chekOut").scrollIntoView();
      }, [cartItems]);
    const increaseQty = (id, quantity, stock) => {
        const newQty = quantity + 1;

        if (newQty > stock) return;

        addItem(id, newQty,setCartLen,setCartItems)
    }

    const decreaseQty = (id, quantity) => {

        const newQty = quantity - 1;

        if (newQty <= 0) return;

        addItem(id, newQty,setCartLen,setCartItems)
    
    }

    const checkoutHandler = () => {
        history.push('/livraison')
    }

    return (
        <>
            <MetaData title={'Votre panier'} />
            {cartItems.length === 0 ? <NotFoundProduct type='panier'/> : (
                <>
                    <span id='chekOut' style={{opacity:'0.0',fontSize:'x-small'}}>aaaaaaa</span>
                    <h2 className="mt-5"  style={{fontSize:'2rem'}} >Votre panier: <b>{cartItems.length} lments</b></h2>

                    <div className="row d-flex justify-content-between">
                        <div className="col-12 col-lg-8">

                            {cartItems.map(item => (
                                <Fragment key={item.product}>
                                    <hr />

                                    <div className="cart-item" >
                                        <div className="row">
                                            <div className="col-4 col-lg-3">
                                                <img src={item.image} alt="Laptop" height="90" width="115" />
                                            </div>

                                            <div className="col-5 col-lg-3">
                                                <Link to={`/produit/${item.product}`}>{item.name}</Link>
                                            </div>


                                            <div className="col-4 col-lg-2 mt-4 mt-lg-0">
                                                <p id="card_item_price">{item.prix} DTN</p>
                                            </div>

                                            <div className="col-4 col-lg-3 mt-4 mt-lg-0">
                                                <div className="stockCounter d-inline">
                                                    <span className="btn btn-danger minus" onClick={() => decreaseQty(item.product, item.qte)}>-</span>

                                                    <input type="number" className="form-control count d-inline" value={item.qte} readOnly />

                                                    <span className="btn btn-primary plus" onClick={() => increaseQty(item.product, item.qte, item.quantite)}>+</span>
                                                </div>
                                            </div>

                                            <div className="col-4 col-lg-1 mt-4 mt-lg-0">
                                                <i id="delete_cart_item" className="fa fa-trash btn btn-danger" onClick={() => removeCartItemHandler(item.product)} ></i>
                                            </div>

                                        </div>
                                    </div>
                                    <hr />
                                </Fragment>
                            ))}

                        </div>

                        <div className="col-12 col-lg-3 my-4">
                            <div id="order_summary">
                                <h4>Rcapitulatif de la commande</h4>
                                <hr />
                                <p>Total:  <span className="order-summary-values">{cartItems.reduce((acc, item) => (acc + Number(item.qte)), 0)} (Units)</span></p>
                                <p>Est. total: <span className="order-summary-values">{cartItems.reduce((acc, item) => acc + item.qte * item.prix, 0).toFixed(2)} DTN</span></p>
                                <hr />
                                <button id="checkout_btn" className="btn btn-primary btn-block" onClick={checkoutHandler}>Vrifier</button>
                            </div>
                        </div>
                    </div>
                </>
            )}
        </>
    )
}

export default Cart