import {useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { MDBDataTable } from 'mdbreact'
import { useHistory } from "react-router-dom";
import MetaData from '../layouts/MetaData'
import Loader from '../layouts/Loader'
import { useAlert } from 'react-alert'
import { myOrders } from '../../actions/cartAction'

const ListOrders = ({loading,connected}) => {
    const [res, setRes] = useState(null);
    const [erreur, setErreur] = useState(null);
    const alert = useAlert();
    let history = useHistory();

    useEffect(() => { 
        myOrders(setRes,setErreur) 
        if(!loading){
        !connected&&history.push('/login') 
        !connected&&alert.error(<div style={{fontSize:'12px'}}>connectez-vous d'abord pour accder  cette ressource</div>) 
        }
    }, [loading]) 
    useEffect(() => {
        if (erreur) {
            alert.error(<div style={{fontSize:'10px'}}>{erreur.errMessage?erreur.errMessage:'Erreur!!'}</div>)
            setErreur(null)
        }
    }, [erreur])

    const setOrders = () => {
        const data = {
            columns: [
                {
                    label: 'Commande ID',
                    field: 'id',
                    sort: 'asc'
                },
                {
                    label: "Nombre d'objets",
                    field: 'numOfItems',
                    sort: 'asc'
                },
                {
                    label: 'Montant',
                    field: 'amount',
                    sort: 'asc'
                },
                {
                    label: 'Statut',
                    field: 'status',
                    sort: 'asc'
                },
                {
                    label: 'Actions',
                    field: 'actions',
                    sort: 'asc'
                },
            ],
            rows: []
        }

        res&&res.commandes.forEach(order => {
            data.rows.push({
                id: order._id,
                numOfItems: order.orderItems.length,
                amount: `${order.prixTotale} DTN`,
                status: order.orderStatus && String(order.orderStatus).includes('Livre')
                    ? <p style={{ color: 'green' }}>{order.orderStatus}</p>
                    : <p style={{ color: 'red' }}>{order.orderStatus}</p>,
                actions:
                    <Link to={`/commande/${order._id}`} align='center' className="btn btn-primary">
                        <i className="fa fa-eye"></i>
                    </Link>
            })
        })

        return data;
    }

    return (
        <>

            <MetaData title={'Mes Commandes'} />

            <h1 className="my-5">Mes Commandes</h1>

            {!res ? <Loader /> : (
                <MDBDataTable
                    data={setOrders()}
                    className="px-3"
                    bordered
                    striped
                    hover
                    entriesLabel='Afficher les entres'
                    infoLabel={['Affichage de ', '  ', ' sur ', ' lignes']}
                    paginationLabel= {['Prcdent', 'Suivant']}
                    searchLabel= 'Chercher'
                    noRecordsFoundLabel='Aucune commande correspondante trouve'
                    responsive
                />
            )}

        </>
    )
}

export default ListOrders