import { Link } from 'react-router-dom'

const Sidebar = () => {
    return (
        <div className="sidebar-wrapper">
            <nav id="sidebar">
                <ul className="list-unstyled components">
                    <li>
                        <Link to="/TableauDebord"><i className="fa fa-tachometer"></i>TableauDeBord</Link>
                    </li>

                    <li>
                        <a href="#productSubmenu" data-toggle="collapse" aria-expanded="false" className="dropdown-toggle"><i
                            className="fa fa-product-hunt"></i> Produits</a>
                        <ul className="collapse list-unstyled" id="productSubmenu">
                            <li>
                                <Link to="/admin/produits"><i className="fa fa-clipboard"></i> Tous</Link>
                            </li>

                            <li>
                                <Link to="/admin/produit"><i className="fa fa-plus"></i> Ajouter</Link>
                            </li>
                        </ul>
                    </li>
                    <li>
                        <Link to="/admin/marques"><i className="fa fa-bookmark"></i> Marques</Link>
                    </li>
                    <li>
                        <Link to="/admin/commandes"><i className="fa fa-shopping-basket"></i> Commandes</Link>
                    </li>

                    <li>
                        <Link to="/admin/utilisateurs"><i className="fa fa-users"></i> Utilisateurs</Link>
                    </li>

                    <li>
                        <Link to="/admin/avis"><i className="fa fa-star"></i> Avis</Link>
                    </li>

                </ul>
            </nav>
        </div>
    )
}

export default Sidebar