import { useState, useEffect } from 'react'
import Loader from '../layouts/Loader'
import MetaData from '../layouts/MetaData'
import { useHistory } from "react-router-dom";
import { useAlert } from 'react-alert'
import {  updateUser } from '../../actions/userAction'
import { Formik} from 'formik';
import * as Yup from 'yup';

const UpdateProfile = ({ user,loading,connected,setUser }) => {
    const [avatar, setAvatar] = useState(null)
    const [avatarPreview, setAvatarPreview] = useState('/images/default_avatar.jpg');
    const [res, setRes] = useState(null);
    const [erreur, setErreur] = useState(null);
    const [loading2, setLoding2] = useState(false);
    let history = useHistory();
    const alert = useAlert();
    const validate = Yup.object({
        name: Yup.string()
          .max(30,"Votre nom ne peut pas dpasser 30 caractres")
          .min(3, 'Votre nom doit tre au moins de 3 caractres ')
          .required("S'il vous plat entrez votre nom"),     
        email: Yup.string()
          .email('votre email est incorrecte')
          .required("S'il vous plat entrez votre email"),
      })

    useEffect(() => {
        user&&user.avatar&&setAvatarPreview(`/${user.avatar}`)
    }, [user])
    
   

    useEffect(() => {
        if(!loading){
        !connected&&history.push('/login') 
        !connected&&alert.error(<div style={{fontSize:'12px'}}>connectez-vous d'abord pour accder  cette ressource</div>) 
        }
    }, [loading,connected,history,alert]) 

    useEffect(() => {
        res&&res.success&&setUser(res.user)
        res&&res.success&&history.push('/monProfil')

    }, [res,history,setUser])

    useEffect(() => {
      
        if (erreur) {
            alert.error(<div style={{fontSize:'10px'}}>{erreur.errMessage?erreur.errMessage:'Erreur!!'}</div>)
            setErreur(null)
        }
    }, [erreur,alert])

    const submitHandler = (e,valid,values) => {
        e.preventDefault();
        if(valid){

        const formData = new FormData();
        formData.append('name',values.name);
        formData.append('email', values.email);
        if(avatar){
            formData.append('avatar', avatar)}
        setLoding2(true)
        updateUser(formData,setRes,setErreur,setLoding2,avatar?user.avatar:'')
      }else{
        alert.error(<div style={{fontSize:'10px'}}>Erreur de remplissage des champs</div>)
      }
    }
    function validateImage(value){
        var idxDot = value.name.lastIndexOf(".") + 1;
        var extFile = value.name.substr(idxDot, value.name.length).toLowerCase();
        if (extFile==="jpg" || extFile==="jpeg" || extFile==="png"){
            return false
        }
        return true
        }

    const onChange = e => {
        if(validateImage(e.target.files[0])){
            alert.error(<div style={{fontSize:'10px'}}>S'il vous plat chosir un fichier de type images</div>)
        }else{
        setAvatar(e.target.files[0])
        const reader = new FileReader();
        reader.onload = () => {
            if (reader.readyState === 2) {
                setAvatarPreview(reader.result)
            }
        }

        reader.readAsDataURL(e.target.files[0])
    }
    }
    return (
        <>
        {loading ? <Loader /> : 
            <>
                <MetaData title={'Mettre  jour le profil'} />

                <div className="row wrapper">
                    <div className="col-10 col-lg-5">
                    <Formik
                        initialValues={{
                            name: user?user.name:'',
                            email: user?user.email:''
                        }}
                        validationSchema={validate} >
                        {formik => ( 
                            <form className="shadow-lg" onSubmit={(e)=>{submitHandler(e,formik.isValid,formik.values)}} encType='multipart/form-data'>
                                <h1 className="mt-2 mb-5"  align='center' style={{fontSize:'2rem'}}>Mettre  jour le profil</h1>

                                <div className="form-group">
                                    <label htmlFor="email_field">Nom</label>
                                    <input
                                        type="name"
                                        id="name_field"
                                        className={(formik.touched.name&&formik.errors.name)?"form-control formError":"form-control"}
                                        name='name'
                                        onBlur={formik.handleBlur}
                                        onChange={formik.handleChange}
                                        value={formik.values.name}
                                    />
                                </div>
                                <div className="alert alert-danger" hidden={!(formik.touched.name&&formik.errors.name)} style={{fontSize:'x-small'}}>
                                        {formik.errors.name}
                                </div>
                                <div className="form-group">
                                    <label htmlFor="email_field">Email</label>
                                    <input
                                        type="email"
                                        id="email_field"
                                        className={(formik.touched.email&&formik.errors.email)?"form-control formError":"form-control"}
                                        name='email'
                                        onBlur={formik.handleBlur}
                                        onChange={formik.handleChange}
                                        value={formik.values.email}
                                    />
                                </div>
                                <div className="alert alert-danger" hidden={!(formik.touched.email&&formik.errors.email)} style={{fontSize:'x-small'}}>
                                        {formik.errors.email}
                                </div>
                                <div className='form-group'>
                                    <label htmlFor='avatar_upload'>Avatar</label>
                                    <div className='d-flex align-items-center'>
                                        <div>
                                            <figure className='avatar mr-3 item-rtl'>
                                                <img
                                                    src={avatarPreview}
                                                    className='rounded-circle'
                                                    alt='Avatar Preview'
                                                />
                                            </figure>
                                        </div>
                                        <div className='custom-file'>
                                            <input
                                                type='file'
                                                name='avatar'
                                                className='custom-file-input'
                                                id='customFile'
                                                accept='image/*'
                                                onChange={onChange}
                                            />
                                            <label className='custom-file-label' htmlFor='customFile'>
                                                Choisir Avatar
                                        </label>
                                        </div>
                                    </div>
                                </div>

                                <button type="submit" disabled={loading2} className="btn update-btn btn-block mt-4 mb-3" >Envoyer</button>
                            </form>)}
                        </Formik>
                    </div>
                </div>
            </>}
        </>    
    )
}

export default UpdateProfile