import  { useState, useEffect } from 'react'
import { Carousel } from 'react-bootstrap'

import Loader from '../layouts/Loader'
import MetaData from '../layouts/MetaData'
import { useAlert } from 'react-alert';
import { getProduct, newReview } from '../../actions/produitsActions';
import { addItem } from '../../actions/cartAction';
import { useRouteMatch} from "react-router-dom";
import ListReviews from '../review/ListReviews';

function ProduitDetaille({loading,connected,setCartLen}) {
    const alert = useAlert();
    const [data, setData] = useState(null)
    const [res, setRes] = useState(null)
    const [loading2, setLoding2] = useState(false);
    const [erreur, setErreur] = useState(null)
    const [qte, setQte] = useState(1)
    const [rating, setRating] = useState(0);
    const [comment, setComment] = useState('');
    let match = useRouteMatch();
    // function topFunction() {
    //     document.getElementById("myhr").scrollIntoView();
    //   }

    useEffect(() => {
        data&& document.getElementById("imgScroll").scrollIntoView();
        setErreur(null)
        
        
    }, [data])
    useEffect(() => {
        getProduct(match.params.id,setData,setErreur);
    }, [loading,res])
    useEffect(() => {
        
        
        return () => {
            setData(null);
            setRes(null);
            setLoding2(false);
            setQte(1);
            setRating(0);
            setComment('');
            setErreur(null);
          };
       
    }, [])

    useEffect(() => {
        if (erreur) {
            alert.error(<div style={{fontSize:'10px'}}>{erreur.errMessage?erreur.errMessage:'Erreur!!'}</div>)
            setErreur(null)
        }
        res&&res.success&&alert.success('Avis ajouter avec succs')
       
    }, [erreur,res])
    
    const addToCart = () => {
        addItem(match.params.id, qte,setCartLen);
        alert.success('Article ajout au panier')  
    }
    const increaseQty = () => {
        const count = document.querySelector('.count')
        if (count.valueAsNumber >= data.produit.quantite) return;
        const qty = count.valueAsNumber + 1;
        setQte(qty)
    }

    const decreaseQty = () => {
        const count = document.querySelector('.count')
        if (count.valueAsNumber <= 1) return;
        const qty = count.valueAsNumber - 1;
        setQte(qty)
    }
    
    function setUserRatings() {
        const stars = document.querySelectorAll('.star');

        stars.forEach((star, index) => {
            star.starValue = index + 1;

            ['click', 'mouseover', 'mouseout'].forEach(function (e) {
                star.addEventListener(e, showRatings);
            })
        })

    function showRatings(e) {
            stars.forEach((star, index) => {
                if (e.type === 'click') {
                    if (index < this.starValue) {
                        star.classList.add('orange');

                        setRating(this.starValue)
                    } else {
                        star.classList.remove('orange')
                    }
                }

                if (e.type === 'mouseover') {
                    if (index < this.starValue) {
                        star.classList.add('yellow');
                    } else {
                        star.classList.remove('yellow')
                    }
                }

                if (e.type === 'mouseout') {
                    star.classList.remove('yellow')
                }
            })
        }
    }
    const reviewHandler = () => {
        setLoding2(true)
        newReview({ratting:rating,comment, produitId:match.params.id},setRes,setErreur,setLoding2);
    }
    return (
        <>
        <hr id='myhr' />
            {!data ? <Loader /> : (
                <>
                    <MetaData title={data.produit.name} />
                    <div className="row d-flex justify-content-around" id="imgScroll">
                        <div className="col-12 col-lg-5 img-fluid" id="product_image">
                            <Carousel pause={false}  >
                                {data.produit.images && data.produit.images.map(image => (
                                    <Carousel.Item key={image._id}>
                                        <img className="d-block w-100" src={image.url} alt={data.produit.title} />
                                    </Carousel.Item>
                                ))}
                            </Carousel>
                        </div>

                        <div className="col-12 col-lg-5 mt-5">
                            <h3>{data.produit.name}</h3>
                            <p id="product_id">refrence  # {data.produit.ref}</p>

                            <hr />

                            <div className="rating-outer">
                                <div className="rating-inner" style={{ width: `${(data.produit.rattings / 5) * 100}%` }}></div>
                            </div>
                            <span id="no_of_reviews">({data.produit.numOfReviews} Avis)</span>

                            <hr />

                            <p id="product_price">{data.produit.prix} DTN</p>
                            <div className="stockCounter d-inline">
                                <span className="btn btn-danger minus" onClick={decreaseQty}>-</span>

                                <input type="number" className="form-control count d-inline"  value={qte} readOnly />

                                <span className="btn btn-primary plus"  onClick={increaseQty}>+</span>
                            </div>
                            <button type="button" id="cart_btn" className="btn btn-primary d-inline ml-4" disabled={data.produit.quantite === 0} onClick={addToCart}>Ajouter au panier</button>

                            <hr />

                            <p>Etat: <span id="stock_status" className={data.produit.quantite > 0 ? 'greenColor' : 'redColor'} >{data.produit.quantite > 0 ? 'en stock' : 'Out of Stock'}</span></p>

                            <hr />

                            <h4 className="mt-2">Description:</h4>
                            <p>{data.produit.description}</p>
                            <hr />
                            {connected ? <button id="review_btn" type="button" className="btn btn-primary mt-4" data-toggle="modal" data-target="#ratingModal" onClick={setUserRatings}>
                            Soumettez votre avis
                            </button>
                                :
                                <div className="alert alert-danger mt-5" type='alert'>Connectez-vous pour poster votre avis.</div>
                            }

                            
                            <div className="row mt-2 mb-5">
                                <div className="rating w-50">

                                    <div className="modal fade" id="ratingModal" tabIndex="-1" role="dialog" aria-labelledby="ratingModalLabel" aria-hidden="true">
                                        <div className="modal-dialog" role="document">
                                            <div className="modal-content">
                                                <div className="modal-header">
                                                    <h5 className="modal-title" id="ratingModalLabel">Envoyer Avis</h5>
                                                    <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                                        <span aria-hidden="true">&times;</span>
                                                    </button>
                                                </div>
                                                <div className="modal-body">

                                                    <ul className="stars" >
                                                        <li className="star"><i className="fa fa-star"></i></li>
                                                        <li className="star"><i className="fa fa-star"></i></li>
                                                        <li className="star"><i className="fa fa-star"></i></li>
                                                        <li className="star"><i className="fa fa-star"></i></li>
                                                        <li className="star"><i className="fa fa-star"></i></li>
                                                    </ul>

                                                    <textarea
                                                        name="review"
                                                        id="review" className="form-control mt-3"
                                                        value={comment}
                                                        onChange={(e) => setComment(e.target.value)}
                                                    >

                                                    </textarea>

                                                    <button className="btn my-3 float-right review-btn px-4 text-white" disabled={loading2} onClick={reviewHandler} data-dismiss="modal" aria-label="Close">Envoyer</button>
                                                </div>
                                            </div>
                                        </div>
                                    </div>

                                </div>
                            </div>
                        </div>
                    </div>

                    {data.produit.reviews && data.produit.reviews.length > 0 && (
                        <ListReviews reviews={data.produit.reviews} />
                    )}

                </>
            )}
        </>
    )
}

export default ProduitDetaille
