import { useState, useEffect } from 'react'

import MetaData from '../layouts/MetaData'
import Sidebar from './Sidebar'

import { useAlert } from 'react-alert'
import { getUser ,updateUserAdmin} from '../../actions/userAction'


const UpdateUser = ({ history, match }) => {

    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [role, setRole] = useState('')
    const [res, setRes] = useState(null)
    const [data,setData] = useState(null)
    const [loading, setLoading] = useState(false)
    const [erreur, setErreur] = useState(null)
    const alert = useAlert();
   


    const userId = match.params.id;
    useEffect(() => {
        getUser(userId,setData,setErreur);
    }, [userId])

    
    useEffect(() => {
        res&&res.success&&history.push('/admin/utilisateurs');
        res&&res.success&&alert.success('Utilisateur mis  jour avec succs');
        
        if(data&&data.success)
            {
                setName(data.user.name);
                setEmail(data.user.email);
                setRole(data.user.role);
            }
        if (erreur) {
            alert.error(erreur.errMessage?erreur.errMessage:'Erreur!!')
            setErreur(null)
        }
    }, [erreur,res,data])
   
    const submitHandler = (e) => {
        e.preventDefault();
        setLoading(true)
        updateUserAdmin(userId, {name,email,role},setRes,setErreur,setLoading)
    }


    return (
        <>
            <MetaData title={`Mettre  jour l'utilisateur`} />
            <div className="row">
                <div className="col-12 col-md-2">
                    <Sidebar />
                </div>

                <div className="col-12 col-md-10">
                    <div className="row wrapper">
                        <div className="col-10 col-lg-5">
                            <form className="shadow-lg" onSubmit={submitHandler}>
                                <h1 className="mt-2 mb-5">Mettre  jour l'utilisateur</h1>

                                <div className="form-group">
                                    <label htmlFor="name_field">Nom Complet</label>
                                    <input
                                        type="name"
                                        id="name_field"
                                        className="form-control"
                                        name='name'
                                        value={name}
                                        onChange={(e) => setName(e.target.value)}
                                    />
                                </div>

                                <div className="form-group">
                                    <label htmlFor="email_field">Email</label>
                                    <input
                                        type="email"
                                        id="email_field"
                                        className="form-control"
                                        name='email'
                                        value={email}
                                        onChange={(e) => setEmail(e.target.value)}
                                    />
                                </div>

                                <div className="form-group">
                                    <label htmlFor="role_field">Role</label>

                                    <select
                                        id="role_field"
                                        className="form-control"
                                        name='role'
                                        value={role}
                                        onChange={(e) => setRole(e.target.value)}
                                    >
                                        <option value="user">user</option>
                                        <option value="admin">admin</option>
                                    </select>
                                </div>

                                <button type="submit" disabled={loading} className="btn update-btn btn-block mt-4 mb-3" >Mettre  jour</button>
                            </form>
                        </div>
                    </div>
                </div>
            </div>

        </>
    )
}

export default UpdateUser
