import {  useState, useEffect } from 'react'

import MetaData from '../layouts/MetaData'
import Sidebar from './Sidebar'
import { getMarks } from '../../actions/markAction';
import { useAlert } from 'react-alert'
import { getProduct , updateProduct} from '../../actions/produitsActions'

const UpdateProduct = ({ match, history }) => {

    const [name, setName] = useState('');
    const [price, setPrice] = useState(0);
    const [description, setDescription] = useState('');
    const [category, setCategory] = useState('Montres');
    const [stock, setStock] = useState(0);
    const [reference, setReference] = useState('');
    const [images, setImages] = useState([]);
    const [imagesPreview, setImagesPreview] = useState([])
    const [loading, setLoading] = useState(false)
    const [erreur, setErreur] = useState(null)
    const [mark, setMark] = useState(null)
    const [marks, setMarks] = useState(null)
    const [res, setRes] = useState(null)
    const [data,setData] = useState(null)
    const [res2, setRes2] = useState(null)
    const [oldImages, setOldImages] = useState([]);
 
    const categories = ['Montres','Lunettes']


    const alert = useAlert();

    const productId = match.params.id;
    useEffect(() => {
        getProduct(productId,setData,setErreur);
        getMarks(setRes,setErreur)
    }, [productId])
    
  
    useEffect(() => {
        res2&&res2.success&&history.push('/admin/produits');
        res2&&res2.success&&alert.success('Produit mis  jour avec succs');
        
        if(data&&data.success){
            setName(data.produit.name);
            setPrice(data.produit.prix);
            setDescription(data.produit.description);
            setCategory(data.produit.category);
            setMark(data.produit.mark);
            setReference(data.produit.ref);
            setStock(data.produit.quantite)
            setOldImages(data.produit.images)
          }

        if(res&&res.success){
            const tab=res.marks.filter(m=>m.category===category)
            setMarks(tab)
          }
        if (erreur) {
            alert.error(erreur.errMessage?erreur.errMessage:'Erreur!!')
            setErreur(null)
        }
    }, [erreur,res,res2,data])
    
    const loadMark=(category)=>{
        const tab=res.marks.filter(m=>m.category===category)
        setMarks(tab)
        setMark(tab[0]._id)
    }

    const submitHandler = (e) => {
        e.preventDefault();
        const formData = new FormData();
        formData.append('name', name);
        formData.append('ref', reference);
        formData.append('prix', price);
        formData.append('description', description);
        formData.append('category', category);
        formData.append('quantite', stock);
        formData.append('mark', mark);
        images.forEach(image => {
            formData.append('images', image)
        })
        
        setLoading(true)
        updateProduct(productId,formData,setRes2,setErreur,setLoading)
    }

    function validateImage(value){
        var idxDot = value.name.lastIndexOf(".") + 1;
        var extFile = value.name.substr(idxDot, value.name.length).toLowerCase();
        if (extFile==="jpg" || extFile==="jpeg" || extFile==="png"){
            return false
        }
        return true
        }
    const onChange = e => {
        let img=[...images]
        let imgP=[...imagesPreview]
        if(validateImage(e.target.files[0])){
            alert.error(<div style={{fontSize:'10px'}}>S'il vous plat chosir un fichier de type images</div>)
        }else{
        
            if(!(img.find(i=>i.name===e.target.files[0].name))){
                img.push(e.target.files[0])
                const reader = new FileReader();
                reader.onload = () => {
                    if (reader.readyState === 2) {
                        imgP.push(reader.result)
                        setImagesPreview(imgP);
                        setImages(img)
                    }
                }
                reader.readAsDataURL(e.target.files[0])     
            }   
        }
        }
       

    const onDeleteImg = (im) => {
  
        let img=[...images]
        let imgP=[...imagesPreview]
        let index=imgP.findIndex(i=>i===im);
        img.splice(index,1);
        imgP.splice(index,1);
        setImagesPreview(imgP);
        setImages(img);
    }


    return (
        <>
            <MetaData title={'Mettre  jour le produit'} />
            <div className="row">
                <div className="col-12 col-md-2">
                    <Sidebar />
                </div>

                <div className="col-12 col-md-10">
                    <>
                        <div className="wrapper my-5">
                            <form className="shadow-lg" onSubmit={(e)=>submitHandler(e)} encType='multipart/form-data'>
                                <h1 className="mb-4">Mettre  jour le produit</h1>

                                <div className="form-group">
                                    <label htmlFor="name_field">Nom</label>
                                    <input
                                        type="text"
                                        id="name_field"
                                        className="form-control"
                                        value={name}
                                        onChange={(e) => setName(e.target.value)}
                                        required
                                    />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="seller_field">Refrence</label>
                                    <input
                                        type="text"
                                        id="seller_field"
                                        className="form-control"
                                        value={reference}
                                        onChange={(e) => setReference(e.target.value)}
                                        required
                                    />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="price_field">Prix</label>
                                    <input
                                        type="text"
                                        id="price_field"
                                        className="form-control"
                                        value={price}
                                        onChange={(e) => setPrice(e.target.value)}
                                        required
                                    />
                                </div>

                                <div className="form-group">
                                    <label htmlFor="description_field">Description</label>
                                    <textarea className="form-control" id="description_field" required rows="8" value={description} onChange={(e) => setDescription(e.target.value)}></textarea>
                                </div>

                                <div className="form-group">
                                    <label htmlFor="category_field">Catgorie</label>
                                    <select className="form-control" id="category_field" value={category} onChange={(e) => {setCategory(e.target.value);loadMark(e.target.value)}}>
                                        {categories.map(category => (
                                            <option key={category} value={category} >{category}</option>
                                        ))}

                                    </select>
                                </div>
                                <div className="form-group">
                                    <label htmlFor="category_field">Marque</label>
                                    <select className="form-control" id="category_field" value={mark||''}  onChange={(e) => setMark(e.target.value)}>
                                        {marks&&marks.map(mark => (
                                            <option key={mark._id} value={mark._id} >{mark.name}</option>
                                        ))}

                                    </select>
                                </div>

                                <div className="form-group">
                                    <label htmlFor="stock_field">Stock</label>
                                    <input
                                        type="number"
                                        id="stock_field"
                                        className="form-control"
                                        value={stock}
                                        onChange={(e) => setStock(e.target.value)}
                                        required
                                    />
                                </div>



                                <div className='form-group'>
                                    <label>Images</label>
                                    <span>
                                        <input
                                            type='file'
                                            name='product_image'
                                            hidden
                                            id='customFile'
                                            accept='image/*'
                                            onChange={onChange}
                                        />
                                        <label className='btnimg' htmlFor='customFile'>
                                            {images.length===0? 'Choisir une image':'Choisir une autre image'}
                                        </label>   
                                    </span>

                                    {oldImages && oldImages.map(img => (
                                        <img key={img._id} src={img.url} alt={img.url} className="mt-3 mr-2" width="55" height="52" />
                                    ))}
                                    <hr/>
                                    {imagesPreview.map(img => (
                                        <span className="mt-3 mr-2"   style={{position:'relative'}} width="55" height="52"  >
                                            <img src={img} key={img} alt="Images Preview" width="55" height="52"   />
                                            <span onClick={()=>{onDeleteImg(img)}}  className='remove-image' style={{display: 'inline',color:'red'}}>&#215;</span>
                                       </span>  
                                    ))}

                                </div>


                                <button
                                    id="login_button"
                                    type="submit"
                                    className="btn btn-block py-3"
                                    disabled={loading }
                                >
                                    METTRE  JOUR
                            </button>

                            </form>
                        </div>
                    </>
                </div>
            </div>

        </>
    )
}

export default UpdateProduct