import  {  useState, useEffect } from 'react'

import MetaData from '../layouts/MetaData'
import Loader from '../layouts/Loader'
import { useAlert } from 'react-alert'
import { useHistory,useRouteMatch } from "react-router-dom";
import { resertPassword } from '../../actions/userAction'
import { Formik} from 'formik';
import * as Yup from 'yup';


const NewPassword = ({ connected,loading }) => {
    const [res, setRes] = useState(null);
    const [erreur, setErreur] = useState(null);
    const [loading2, setLoding2] = useState(false);
    const alert = useAlert();
    let history = useHistory();
    let match = useRouteMatch();
    const validate = Yup.object({
        confirmPassword: Yup.string()   
          .required("S'il vous plat entrez votre ancien mot de passe"),
        password: Yup.string()
          .matches(/^.*[0-9].*$/,"votre nouveau mot de passe bessoin d'un chiffre")
          .min(8, 'Le nouveau mot de passe doit tre au moins de 8 caractres')    
          .required("S'il vous plat entrez votre nouveau mot de passe"), 
      })
      useEffect(() => {    
        document.getElementById('password_field').focus()
    }, [])    
    useEffect(() => {
        setLoding2(!loading)
        connected&&history.push('/')
        res&&res.success&&history.push('/login') 
        res&&res.success&&alert.success(<div style={{fontSize:'10px'}}>Password updated successfully</div>)    
    }, [res,connected,loading])

    useEffect(() => {
        if (erreur) {
            alert.error(<div style={{fontSize:'10px'}}>{erreur.errMessage?erreur.errMessage:'Erreur!!'}</div>)
            setErreur(null)
        }
    }, [erreur])

    const submitHandler = (e,valid,values) => {
        e.preventDefault();
        if(valid){
            setLoding2(true)
            resertPassword(values,match.params.token,setRes,setErreur,setLoding2)
        }else{
            alert.error(<div style={{fontSize:'10px'}}>Erreur de remplissage des champs</div>)
        }
    }

    return (
        <>
        {loading2 ? <Loader /> :
            <>
                <MetaData title={'Rinitialisation du nouveau mot de passe'} />
                <div className="row wrapper">
                    <div className="col-10 col-lg-5">
                    <Formik
                                initialValues={{
                                    password:'',
                                    confirmPassword:''
                                }}
                                validationSchema={validate} >
                        {formik => ( 
                            <form className="shadow-lg" onSubmit={(e)=>{submitHandler(e,formik.isValid,formik.values)}}>
                                <h1 className="mb-3">nouveau mot de passe</h1>

                                <div className="form-group">
                                    <label htmlFor="password_field">Mot de passe</label>
                                    <input
                                        type="password"
                                        id="password_field"
                                        className={(formik.touched.password&&formik.errors.password)?"form-control formError":"form-control"}
                                        name='password'
                                        onBlur={formik.handleBlur}
                                        onChange={formik.handleChange}
                                        value={formik.values.password}   
                                    />
                                </div>
                                <div className="alert alert-danger" hidden={!(formik.touched.password&&formik.errors.password)} style={{fontSize:'x-small'}}>
                                                {formik.errors.password}
                                </div>
                                <div className="form-group">
                                    <label htmlFor="confirm_password_field">Confirmez le mot de passe</label>
                                    <input
                                        type="password"
                                        id="confirm_password_field"
                                        className={(formik.touched.confirmPassword&&formik.errors.confirmPassword)?"form-control formError":"form-control"}
                                        name='confirmPassword'
                                        onBlur={formik.handleBlur}
                                        onChange={formik.handleChange}
                                        value={formik.values.confirmPassword}
                                    
                                    />
                                </div>
                                <div className="alert alert-danger" hidden={!(formik.touched.confirmPassword&&formik.errors.confirmPassword)} style={{fontSize:'x-small'}}>
                                                {formik.errors.confirmPassword}
                                </div>
                                <button
                                    id="new_password_button"
                                    type="submit"
                                    className="btn btn-block py-3">
                                    Valider
                                </button>

                            </form>)}
                        </Formik>    
                    </div>
                </div>

            </>}
        </>    
    )
}

export default NewPassword