import { Link } from 'react-router-dom'

const CheckoutSteps = ({ livraison, confirmOrder, attention ,auth}) => {
    return (
        <div className="checkout-progress d-flex justify-content-center mt-5" >

            {livraison ? <Link to='livraison' className="float-right">
                <div className="triangle2-active"></div>
                <div className="step active-step">Livraison</div>
                <div className="triangle-active"></div>
            </Link> : <Link to="#!" disabled>
                    <div className="triangle2-incomplete"></div>
                    <div className="step incomplete">livraison</div>
                    <div className="triangle-incomplete"></div>
            </Link>}

            {confirmOrder ? <Link to='/confimerCommande' className="float-right">
                <div className="triangle2-active"></div>
                <div className="step active-step">Confirmer la commande</div>
                <div className="triangle-active"></div>
            </Link> : <Link to="#!" disabled>
                    <div className="triangle2-incomplete"></div>
                    <div className="step incomplete">Confirmer la commander</div>
                    <div className="triangle-incomplete"></div>
            </Link>}
            {!auth&&
                <>
                    {attention ? <Link to='/attCommande' className="float-right">
                        <div className="triangle2-active"></div>
                        <div className="step active-step">Attention</div>
                        <div className="triangle-active"></div>
                    </Link> : <Link to="#!" disabled>
                            <div className="triangle2-incomplete"></div>
                            <div className="step incomplete">Attention</div>
                            <div className="triangle-incomplete"></div>
                    </Link>}
                </>}
        </div>
    )
}

export default CheckoutSteps