import React from 'react'

const NotFoundProduct = ({type}) => (
  <>
                {!type?<div style={{margin:'4% auto'}}>
                  <img  src="/images/noProduct.png" alt="accun produit " width='120%' /> 
                  
  
                </div>
:
                <div className="row justify-content-center">
                <div className="col-6 mt-5 text-center">
                    <img className="my-5 img-fluid d-block mx-auto" src="/images/noCart.png" alt="Panier vide" width="200" height="200" />

                    <h2>Votre Panier  est vide.</h2>

                </div>

              </div>
}
   
  </>
);

export default NotFoundProduct;