import  { useState, useEffect } from 'react'
import MetaData from '../layouts/MetaData'
import { useAlert } from 'react-alert'
import { useHistory } from "react-router-dom";
import {  updatePassword } from '../../actions/userAction'
import { Formik} from 'formik';
import * as Yup from 'yup';
import Loader from '../layouts/Loader';


const UpdatePassword = ({ loading,connected }) => {
    const alert = useAlert();
    let history = useHistory();
    const [res, setRes] = useState(null);
    const [erreur, setErreur] = useState(null);
    const [loading2, setLoding2] = useState(false);

    const validate = Yup.object({
        oldPassword: Yup.string()   
          .required("S'il vous plat entrez votre ancien mot de passe"),
        password: Yup.string()
          .matches(/^.*[0-9].*$/,"votre nouveau mot de passe bessoin d'un chiffre")
          .min(8, 'Le nouveau mot de passe doit tre au moins de 8 caractres')    
          .required("S'il vous plat entrez votre nouveau mot de passe"), 
      })

      useEffect(() => {
        if(!loading){
        !connected&&history.push('/login') 
        !connected&&alert.error(<div style={{fontSize:'12px'}}>connectez-vous d'abord pour accder  cette ressource</div>) 
        }
    }, [loading]) 
    useEffect(() => {
        res&&res.success&&alert.success('Mot de passe mis  jour avec succs')
        res&&res.success&&history.push('/monProfil')

    }, [res])

    useEffect(() => {
        if (erreur) {
            alert.error(<div style={{fontSize:'10px'}}>{erreur.errMessage?erreur.errMessage:'Erreur!!'}</div>)
            setErreur(null)
        }
    }, [erreur])
    const submitHandler = (e,valid,values) => {
        e.preventDefault();
    
        if(valid){
            setLoding2(true)
            updatePassword(values,setRes,setErreur,setLoding2)
        }else{
            alert.error(<div style={{fontSize:'10px'}}>Erreur de remplissage des champs</div>)
        }
    }

    return (
        <>
            {loading ? <Loader /> :
            <>
                <MetaData title={'Changer le mot de passe'} />

                <div className="row wrapper">
                    <div className="col-10 col-lg-5">
                    <Formik
                            initialValues={{
                                oldPassword:'',
                                password:''
                            }}
                            validationSchema={validate} >
                        {formik => ( 
                            <form className="shadow-lg" onSubmit={(e)=>{submitHandler(e,formik.isValid,formik.values)}}>
                                <h1 className="mt-2 mb-5"  align='center' style={{fontSize:'2rem'}}>Mettre  jour le mot de passe</h1>
                                <div className="form-group">
                                    <label for="old_password_field">Ancien mot de passe</label>
                                    <input
                                        type="password"
                                        id="old_password_field"
                                        className={(formik.touched.oldPassword&&formik.errors.oldPassword)?"form-control formError":"form-control"}
                                        name='oldPassword'
                                        onBlur={formik.handleBlur}
                                        onChange={formik.handleChange}
                                        value={formik.values.oldPassword}
                                    />
                                </div>
                                <div className="alert alert-danger" hidden={!(formik.touched.oldPassword&&formik.errors.oldPassword)} style={{fontSize:'x-small'}}>
                                            {formik.errors.oldPassword}
                                </div>
                                <div className="form-group">
                                    <label for="new_password_field">nouveau mot de passe</label>
                                    <input
                                        type="password"
                                        id="new_password_field"
                                        className={(formik.touched.password&&formik.errors.password)?"form-control formError":"form-control"}
                                        name='password'
                                        onBlur={formik.handleBlur}
                                        onChange={formik.handleChange}
                                        value={formik.values.password}
                                    />
                                </div>
                                <div className="alert alert-danger" hidden={!(formik.touched.password&&formik.errors.password)} style={{fontSize:'x-small'}}>
                                            {formik.errors.password}
                                </div>
                                <button type="submit" className="btn update-btn btn-block mt-4 mb-3" disabled={loading2} >Valider</button>
                            </form>)}
                        </Formik>    
                    </div>
                </div>

            </>}
        </>    
    )
}

export default UpdatePassword